/* stylelint-disable primer/no-unused-vars */
.btn-container {
  display: flex;
}
.btn-container button {
  margin: 0 0.5em 0 0;
  text-transform: uppercase;
}
.btn-container > button:last-of-type {
  margin: 0;
}
.btn-container button:not([hidden]) {
  flex: auto;
}

.spinner-container {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  cursor: wait;
  background: rgba(248, 249, 250, 0.33);
  justify-content: center;
  align-items: center;
}

.form-check-label {
  margin-bottom: 1.5rem;
  font-size: 80%;
}

.form-check-input {
  margin-top: 0.2rem;
}

.new-tab-button {
  padding: 0.375rem 0.75rem;
  margin: 0 0.5em 0 0;
  font-size: 16px;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .btn-container {
    flex-direction: column;
  }
  .btn-container button {
    width: 100%;
    margin: 0 0 0.5em;
  }
  .btn-container > button:last-of-type {
    margin: 0;
  }

  .form-check-label {
    margin-bottom: 0;
  }

  .new-tab-button {
    width: 100%;
    margin: 0 0 0.5em;
  }
}
.caution-text {
  max-width: 32em;
  font-size: 0.9rem;
  text-align: left;
}

.custom-switch {
  border: 0;
  box-shadow: none;
}

.container-goto-results {
  position: fixed;
  bottom: 10px;
  z-index: 1070;
}

.btn-goto-results {
  box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.25);
}

.recharts-legend-item-text {
  vertical-align: middle;
}