/* stylelint-disable primer/no-unused-vars */
.help-button {
  display: flex;
  width: 16px;
  height: 16px;
  padding: 0;
  background-color: #e9ecef;
  border: 0;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
}

.help-button-icon {
  font-family: "Roboto Mono", "system-mono";
  font-size: 13.6px;
  font-weight: bold;
  color: #6c757d;
  text-align: center;
  vertical-align: middle;
}

.card.card-help {
  border-radius: 3px;
}
.card.card-help .card-body.card-body-help p {
  color: #495057;
}