/* stylelint-disable primer/no-unused-vars */
@media not print {
  .outcomes-table-wrapper:not(.print-preview) {
    height: 500px;
    overflow: auto;
  }

  .outcomes-table:not(.print-preview) {
    min-width: 800px;
    table-layout: fixed;
  }
  .outcomes-table th,
.outcomes-table td,
.outcomes-table tr {
    text-align: center;
    vertical-align: middle;
  }

  .outcome-table-th {
    text-align: center;
  }

  .outcome-table-th-date {
    width: 9rem;
  }
}
