/* stylelint-disable primer/no-unused-vars */
.scenario-title-input {
  min-height: 70px;
  overflow: hidden;
  font-size: 1.5em;
  font-weight: bold;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.scenario-title-input:read-only {
  background-color: #e9ecef;
  border-color: transparent;
  box-shadow: none;
}

.scenario-title-input-group {
  border-color: transparent;
  box-shadow: none;
}

.scenario-title-btn {
  background-color: transparent;
  border-color: transparent;
  box-shadow: none;
}

.scenario-title-icon {
  font-size: 1.5em;
  fill: #6c757d;
}
.scenario-title-icon.scenario-title-icon-cancel {
  fill: #e51c23;
}
.scenario-title-icon.scenario-title-icon-confirm {
  fill: #4caf50;
}