/* stylelint-disable primer/no-unused-vars */
.modal-dialog.export-modal {
  max-width: 600px;
}

.export-file-icon-container {
  width: 50px;
  min-width: 50px;
  max-width: 50px;
}
.export-file-icon-container svg {
  filter: drop-shadow(3px 3px 3px rgba(0, 0, 0, 0.4));
}

.btn-download svg {
  fill: #6c757d;
  filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.4));
}

.export-file-filename {
  font-size: 1em;
}