/* stylelint-disable primer/no-unused-vars */
.scenario-loader-uploader-container {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.case-counts-uploader-error-list {
  max-height: 50vh;
  overflow: scroll;
  flex: 1;
}

.case-counts-uploader-error-list-item {
  margin: 0.25em 0;
  overflow-wrap: anywhere;
  flex: 1;
}

.upload-zone {
  cursor: pointer;
  background-color: #f8f9fa;
  border: 5px #adb5bd dashed;
}
.upload-zone.upload-zone-active {
  border-style: dotted;
}

.upload-icon {
  fill: #adb5bd;
}