/* stylelint-disable primer/no-unused-vars */
.btn-simulation-controls {
  margin-left: 3px;
}
@media screen and (min-width: 992px) {
  .btn-simulation-controls {
    width: 59.985px;
    height: 45px;
  }
  .btn-simulation-controls svg {
    fill: #6c757d;
  }
  .btn-simulation-controls.btn-run {
    width: 90px;
  }
  .btn-simulation-controls.btn-run svg {
    fill: #f8f9fa;
  }
}