/* stylelint-disable primer/no-unused-vars */
.form-main .animate-flex-width {
  transition: flex-basis 250ms ease, width 250ms ease, max-width 250ms ease;
}
.form-main .card {
  background-color: transparent;
}
.form-main .card h2 {
  font-size: 1.5rem;
  color: #000;
  letter-spacing: 0.33em;
  vertical-align: middle;
}
.form-main .card h3 {
  font-size: 1.1rem;
}
.form-main .card .card.card-results h3 {
  margin-top: 1em;
  margin-bottom: 0;
  color: #000;
}
.form-main .card-header.card-with-controls-header,
.form-main .card-header.card-header-collapsible {
  padding: 0.4rem 1rem;
  background-color: #495057;
}
.form-main .card-header.card-with-controls-header h2,
.form-main .card-header.card-header-collapsible h2 {
  color: #495057;
}
.form-main .card-header.card-with-controls-header h3,
.form-main .card-header.card-with-controls-header p,
.form-main .card-header.card-header-collapsible h3,
.form-main .card-header.card-header-collapsible p {
  color: #e9ecef;
}
.form-main .card-header.card-with-controls-header h2,
.form-main .card-header.card-with-controls-header h3,
.form-main .card-header.card-header-collapsible h2,
.form-main .card-header.card-header-collapsible h3 {
  text-transform: uppercase;
}
.form-main .card-header.card-with-controls-header label,
.form-main .card-header.card-header-collapsible label {
  margin: 0;
}
.form-main .card-header-button {
  width: 100%;
  height: 100%;
}
.form-main .card-body.card-with-controls-body {
  padding: 1rem;
}
.form-main .card-scenario,
.form-main .card-results {
  margin-top: 1em;
  border: 0;
  box-shadow: none;
}
.form-main .card-scenario > .card-header,
.form-main .card-results > .card-header {
  min-height: 60px;
  padding: 10px auto;
  color: #6c757d;
  background-color: transparent;
  border-bottom: 2px solid #6c757d;
}
.form-main .card-scenario > .card-header label,
.form-main .card-results > .card-header label {
  margin-top: auto;
  margin-bottom: auto;
}
.form-main .card-scenario > .card-body,
.form-main .card-scenario > .collapse > .card-body,
.form-main .card-results > .card-body,
.form-main .card-results > .collapse > .card-body {
  padding: 0;
  margin-top: 10px;
}
.form-main .btn-results-expand {
  width: 45px;
  height: 45px;
}
.form-main .row-scenario-title,
.form-main .row-scenario-population-epidemiological,
.form-main .row-scenario-mitigation,
.form-main .row-scenario-severity,
.form-main .row-results-trajectories,
.form-main .row-results-age-distribution,
.form-main .row-results-outcomes-table,
.form-main .row-results-outcomes {
  margin-bottom: 0.66em;
}
.form-main .card-population,
.form-main .card-epidemiology,
.form-main .card-mitigation,
.form-main .card-severity,
.form-main .card-trajectories,
.form-main .card-age-distribution,
.form-main .card-outcomes-table,
.form-main .card-outcomes {
  margin-right: 0.25em;
  margin-left: 0.25em;
  background-color: #f8f9fa;
}
.form-main .card-population .card-body .row,
.form-main .card-epidemiology .card-body .row,
.form-main .card-mitigation .card-body .row,
.form-main .card-severity .card-body .row,
.form-main .card-trajectories .card-body .row,
.form-main .card-age-distribution .card-body .row,
.form-main .card-outcomes-table .card-body .row,
.form-main .card-outcomes .card-body .row {
  align-items: center;
  display: flex;
  margin-bottom: 0.5em;
}
.form-main .row.row-form-main {
  margin-right: -5px;
  margin-left: -5px;
}
.form-main .row.row-form-main > .col,
.form-main .row.row-form-main > [class*=col-] {
  padding-right: 5px;
  padding-left: 5px;
}