/* stylelint-disable primer/no-unused-vars */
@media screen and (min-width: 768px) {
  .modal-dialog.scenario-loader-modal {
    width: 90vw;
    max-width: 1200px;
  }
}
.modal-dialog.scenario-loader-modal .modal-header {
  padding: 0.45em 0.85em;
}
@media screen and (max-width: 768px) {
  .modal-dialog.scenario-loader-modal {
    width: 100vw;
    height: 100vh;
    padding: 0;
    margin: 0;
  }
  .modal-dialog.scenario-loader-modal .modal-content {
    position: absolute;
    top: 0;
    padding: 0;
    margin: 0;
    overflow: scroll;
    overflow: overlay;
  }
  .modal-dialog.scenario-loader-modal .modal-header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    width: 99vw;
    background-color: #e9ecef;
  }
  .modal-dialog.scenario-loader-modal .modal-body {
    padding: 0;
    margin-top: 45px;
  }
}

.modal-content {
  background-color: #e9ecef;
}

.scenario-loader-card.card {
  margin: 0 auto;
  background-color: #fff;
  border-width: 0;
  box-shadow: 1px 1px 3px 3px #ced4da;
}
@media screen and (min-width: 992px) {
  .scenario-loader-card {
    height: 85vh;
  }
}
@media screen and (min-width: 768px) {
  .scenario-loader-card {
    height: 80vh;
  }
}
@media screen and (max-width: 768px) {
  .scenario-loader-card .card-body {
    padding: 0 auto;
    margin: 0 auto;
  }
  .scenario-loader-card.scenario-loader-card-list {
    height: 75vh;
    margin-bottom: 4px;
  }
  .scenario-loader-card.scenario-loader-card-uploader {
    min-height: 50vh;
    margin: 0;
    margin-bottom: 4px;
  }
}

.scenario-loader-list-container {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.scenario-loader-list-input-group {
  display: flex;
  flex-grow: 0;
  flex-shrink: 0;
}

.scenario-loader-list {
  display: flex;
  flex-grow: 1;
  overflow: scroll;
  overflow: overlay;
  flex-direction: column;
  border: 1px #ced4da solid;
}

.scenario-loader-list-item {
  cursor: pointer;
}
.scenario-loader-list-item ::selection,
.scenario-loader-list-item :active,
.scenario-loader-list-item :hover {
  background-color: #c1e2fc;
}

.scenario-loader-uploader-container {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.scenario-loader-uploader-dropzone-container,
.scenario-loader-uploader-error-list {
  display: flex;
  flex-grow: 1;
}

.scenario-loader-uploader-dropzone-container {
  flex-basis: 75%;
  flex-shrink: 1;
}

.scenario-loader-uploader-error-list {
  overflow: scroll;
  overflow: overlay;
  flex-direction: column;
}

.scenario-loader-uploader-error-list-item {
  margin: 0.25em 0;
  overflow-wrap: anywhere;
}

.scenario-loader-button {
  width: 80px;
  min-height: 45px;
}
@media screen and (max-width: 992px) {
  .scenario-loader-button {
    width: 100%;
  }
}

.upload-zone {
  cursor: pointer;
  background-color: #f8f9fa;
  border: 5px #adb5bd dashed;
}
.upload-zone.upload-zone-active {
  border-style: dotted;
}

.upload-icon {
  fill: #adb5bd;
}